/* src/App.css */
.App {
  font-family: Arial, sans-serif;
  text-align: center;
  padding: 20px;
}

h1 {
  color: #333;
}

div {
  margin: 10px 0;
}

input {
  margin: 5px;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

button {
  padding: 8px 16px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: #218838;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.app-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.tree-form-container {
  display: flex;
  align-items: flex-start;
  gap: 20px;
  width: 100%;
  transition: all 0.3s ease-in-out;
}

.tree-container {
  flex: 2;
  min-width: 60%;
  height: 500px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid #ddd;
  border-radius: 10px;
  background-color: #f9f9f9;
  padding: 10px;
  overflow: auto;
  transition: all 0.5s ease-in-out; /* Smooth tree resizing */
}

/* When form is visible: Tree is 70% width */
.form-visible .tree-container {
  flex: 2;
  min-width: 60%;
  max-width: 70%;
}

/* When form is hidden: Tree expands to full width */
.form-hidden .tree-container {
  flex: 2;
  min-width: 90%;
  max-width: 90%;
  margin: 0;
}

.form-container {
  flex: 1;
  min-width: 300px;
  max-width: 400px;
  padding: 20px;
  border-radius: 8px;
  background-color: #ffffff;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1);
  text-align: center;
  transition: transform 0.5s ease-in-out, opacity 0.5s ease-in-out;

  form {
    display: flex;
    flex-direction: column;
  }

  form input,
  form button,
  form label {
    margin-bottom: 10px;
  }

  button {
    padding: 10px;
    background-color: #4caf50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }

  button:hover {
    background-color: #45a049;
  }

  /* Toggle Form Button */
  .toggle-form-btn {
    width: 100%;
    padding: 10px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
  }

  .toggle-form-btn:hover {
    background-color: #0056b3;
  }

  /* Expandable Form */
  .expandable-form {
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    transition: all 0.3s ease-in-out;
  }

  .expandable-form input,
  .expandable-form button {
    margin-bottom: 10px;
  }
}

/* When form is hidden: Slide Out + Fade Out */
.form-hidden .form-container {
  transform: translateX(100%); /* Moves it out of view */
  opacity: 0; /* Fades out */
  pointer-events: none; /* Prevents interaction while hidden */
}

/* Show form with slide-in effect */
.form-visible .form-container {
  transform: translateX(0);
  opacity: 1;
}

/* Floating Add Button - Keep it smooth */
.floating-add-btn {
  position: fixed;
  bottom: 20px;
  right: 20px;
  background-color: #007BFF;
  color: white;
  border: none;
  border-radius: 50px;
  padding: 15px 20px;
  font-size: 16px;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease-in-out;
}

.floating-add-btn:hover {
  background-color: #0056b3;
  transform: scale(1.1);
}