/* Modal Background Overlay */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  backdrop-filter: blur(5px); /* Smooth blur */
  opacity: 0;
  animation: fadeIn 0.3s forwards;
}

/* Modal Content */
.modal-content {
  background: white;
  padding: 20px;
  border-radius: 8px;
  text-align: center;
  position: relative;
  width: 300px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transform: translateY(20px);
  opacity: 0;
  animation: slideUp 0.3s forwards;
}

/* Close Button */
.close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  border: none;
  font-size: 18px;
  cursor: pointer;
}

/* Member & Spouse Side by Side */
.member-container {
  display: flex;
  justify-content: space-around; /* Spreads them evenly */
  align-items: center;
  gap: 20px; /* Space between member and spouse */
  padding: 15px 0;
  position: relative;
}

/* Individual Member Info */
.member-info {
  text-align: center;
  flex: 1; /* Makes both take equal space */
  width: 120px;
}

/* Member and Spouse Photos */
.member-photo,
.spouse-photo {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  border: 2px solid #333;
  margin-bottom: 5px;
}

/* Spouse Section - Initially Hidden */
.spouse-info {
  display: none; /* Initially hidden to avoid flicker */
  flex-direction: column;
  align-items: center;
  width: 120px;
  text-align: center;
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
}

/* When Spouse Exists - Make it Visible */
.spouse-info.visible {
  display: flex; /* Show when class is applied */
  opacity: 1;
  transform: scale(1);
}

/* Heart Symbol */
.heart {
  font-size: 20px;
  display: block;
  margin-bottom: 5px;
  background: white; /* Prevent overlap with images */
}

/* Children Section */
.children-section {
  text-align: left;
  margin-top: 15px;
}

.children-section h3 {
  margin-bottom: 5px;
}

.children-section ul {
  list-style: none;
  padding: 0;
}

.children-section li {
  font-size: 14px;
  margin: 5px 0;
}

/* Animations */
@keyframes fadeIn {
  to {
    opacity: 1;
  }
}

@keyframes slideUp {
  to {
    transform: translateY(0);
    opacity: 1;
  }
}
